import { useState } from 'react';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faGlobe} from '@fortawesome/free-solid-svg-icons';

import './css/MainPage.css';

import langJson from '../json/language.json';
import userJpg from '../imgs/user.jpg';
import heFlag from '../imgs/he_flag.png';
import enFlag from '../imgs/en_flag.png';

function MainPage() {
    const [lang, setLang] = useState('he');
    
    const globeIcon = () => {
        return <FontAwesomeIcon dir={langJson[lang][0]} icon={faGlobe} style={{transform: `translate(${lang == 'en' ? '-' : ''}150%, 35%)`, width: '16px'}} className="ml-1 position-absolute"/>;
    }

    return (
        <div>
            <div className="bg-info w-100">
                <div style={{height: '225px'}} className="d-flex" dir={langJson[lang][0]}>
                    <img type="button" className="position-absolute langBtn" style={{right: lang == 'he' && '20px', left: lang == 'en' && '20px', top: '20px'}} onClick={() => setLang(lang == 'en' ? 'he' : 'en')} src={lang == 'en' ? enFlag : heFlag}/>
                    <img src={userJpg} className="m-3" style={{borderRadius: '50%'}}/>
                    <table className={langJson[lang][1]} style={{height: '112.5px', transform: 'translateY(56.25px)'}}>
                        <tbody>
                            <tr>{`${langJson[lang][2]}`}: <b>{`${langJson[lang][3]}`}</b></tr>
                            <tr>{`${langJson[lang][4]}:`} <b>{`${langJson[lang][5]}`}</b></tr>
                            <tr>{`${langJson[lang][6]}:`} <b>{`${langJson[lang][7]}`}</b></tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div dir={langJson[lang][0]} className={`mx-4 ${langJson[lang][1]}`}>
                <p style={{fontSize: '28px', fontWeight: '500'}}><u>{`${langJson[lang][8]}:`}</u></p>
                <ul className="list-unstyled p-0">
                    <li><a target="_blank" href="https://saharzzz.github.io/ecl/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][9]}.</a></li>
                    <li><a target="_blank" href="https://saharzzz.github.io/shoppinglistreact/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][10]}.</a></li>
                    <li><a target="_blank" href="https://saharzzz.github.io/sapp/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][11]}.</a></li>
                    <li><a target="_blank" href="https://saharzzz.github.io/notes/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][12]}.</a></li>
                    <li><a target="_blank" href="https://saharzzz.github.io/tictactoemultiplayer/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][13]}.</a></li>
                    <li><a target="_blank" href="https://saharzzz.github.io/dbzgame/" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][14]}.</a></li>
                    <li><a target="_blank" href="https://masaryk-square-exhibition.s3.eu-central-1.amazonaws.com/index.html" className={`btn webBtn w-100 ${langJson[lang][1]}`}>{globeIcon()}{langJson[lang][15]}.</a></li>
                </ul>
            </div> 
            <div className="text-center">test2</div>
        </div>
    )
}

export default MainPage;